.bg-slider {
  background-color: #000;
  position: fixed;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
}
.loading {
  color: #FFF;
  width: 100%;
  position: absolute;
  height: 100%;
  z-index: 10;
}
.loading .fa {
  opacity: .5;
  filter: alpha(opacity=50);
  position: absolute;
  top: 50%;
  left: 50%;
  margin-left: -25px;
  margin-top: -25px;
  display: inline-block;
}
.pagination {
  margin: 0;
}
.is-table {
  width: 100%;
  display: table;
  padding: 0px;
}
.is-table-col {
  display: table-cell;
  float: none;
  padding: 0px;
}
.is-table-col table {
  margin-bottom: 0px;
}
.is-table-col .bg-slider {
  background-color: #EEE;
  position: absolute;
}
.is-table-col .bg-slider .fa {
  color: #555;
}
.alert {
  margin: 6px 0px 0px 0px;
}
.alert .close {
  margin-top: -6px;
}
.alert .alert-right {
  padding-left: 20px;
  vertical-align: middle;
  display: table-cell;
}
.alert .alert-right strong {
  text-transform: capitalize;
}
.alert .alert-right p {
  margin: 0;
}
.alert .alert-left {
  width: 1%;
  vertical-align: middle;
  display: table-cell;
}
.alert .alert-left i {
  @include opacity(.5);
  font-size: 30px;
}
